.profile__data {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 16px 0 0 0;
  font-family: 'Inter', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 13px;
  color: #ffffff;
}

.profile__data:first-child {
  padding-top: 0;
}

.profile__data:nth-last-of-type(2) {
  border: none;
}
