.about-me__photo {
  max-width: 270px;
  max-height: 327px;
  object-fit: contain;
}

@media (max-width: 870px) {
  .about-me__photo {
    max-width: 255px;
    max-height: 307px;
  }
}

@media (max-width: 500px) {
  .about-me__photo {
    max-width: 292px;
    max-height: 352px;
  }
}
